<template>
  <div class="app flex-row align-items-center">
    <div class="container">
      <div class="row justify-content-center">
        <div class="col-md-6">
          <div class="card mx-4">
            <div class="card-body p-4">
              <h1></h1>
              <p class="text-muted"></p>
              <div class="input-group mb-3">
                <el-input size="medium" placeholder="" v-model="userID" @keyup.enter.native="detectKeyup">
                  <template slot="prepend">
                    <i class="icon-lock"></i>
                  </template>
                </el-input>
              </div>
              <div class="input-group mb-3">
                <el-input size="medium" placeholder="" v-model="oldPwd" type="password" @keyup.enter.native="detectKeyup">
                  <template slot="prepend">
                    <i class="icon-lock"></i>
                  </template>
                </el-input>
              </div>
              <div class="input-group mb-3">
                <el-input size="medium" placeholder="" v-model="newPwd" type="password" @keyup.enter.native="detectKeyup">
                  <template slot="prepend">
                    <i class="icon-lock"></i>
                  </template>
                </el-input>
              </div>
              <div class="input-group mb-4">
                <el-input size="medium" placeholder="" v-model="newPwdConfirmed" type="password"
                  @keyup.enter.native="detectKeyup">
                  <template slot="prepend">
                    <i class="icon-lock"></i>
                  </template>
                </el-input>
              </div>
              <el-button type="primary" size="medium" style="width: 100%;" @click="resetPwd"></el-button>
              <div class="mb-3"></div>
              <el-button size="medium" style="width: 100%;" @click="cancel"></el-button>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import {
    changePassword
  } from "@/assets/js/iFarmer/manager/authManager";

  export default {
    data() {
      return {
        userID: "",
        oldPwd: "",
        newPwd: "",
        newPwdConfirmed: ""
      };
    },
    methods: {
      resetPwd: function () {
        if (!this.userID) {
          this.$notify.error({
            title: "",
            message: ""
          });

          this.oldPwd = this.newPwd = this.newPwdConfirmed = "";
          return;
        }

        if (!this.oldPwd) {
          this.$notify.error({
            title: "",
            message: ""
          });

          this.oldPwd = this.newPwd = this.newPwdConfirmed = "";
          return;
        }
        if (!this.newPwd) {
          this.$notify.error({
            title: "",
            message: ""
          });

          this.oldPwd = this.newPwd = this.newPwdConfirmed = "";
          return;
        }
        if (this.newPwd !== this.newPwdConfirmed) {
          this.$notify.error({
            title: "",
            message: ", "
          });

          this.oldPwd = this.newPwd = this.newPwdConfirmed = "";
          return;
        }

        let vueInstance = this;

        changePassword(this.userID, this.oldPwd, this.newPwd, function (result) {
          if (result === true) {
            vueInstance.$notify.success({
              title: "",
              message: ", "
            });
            vueInstance.$router.push({
              name: "Login"
            });
          } else {
            vueInstance.$notify.error({
              title: "",
              message: ", "
            });

            vueInstance.oldPwd = vueInstance.newPwd = vueInstance.newPwdConfirmed =
              "";
          }
        });
      },
      detectKeyup: function () {
        this.resetPwd();
      },
      cancel: function () {
        this.$router.push({
          name: "Login"
        });
      }
    }
  };

</script>

<style>

</style>



